import React from 'react'
import { inject, observer, PropTypes } from 'mobx-react'

import {
  EuiBasicTable,
  EuiText,
} from '@elastic/eui'

import Cashflow from '../../../Models/Cashflow'

const formatMoney = value => (
  <EuiText
    color={value >= 0 ? 'default' : 'danger'}
  >
    {value.toFixed(1)}
  </EuiText>
)


const formatPercentage = value => (
  <EuiText
    color={value >= 0 ? 'default' : 'danger'}
  >
    {value.toFixed(2)}%
  </EuiText>
)


const SimulatedTable = ({ paramStore }) => {
  const items = Cashflow(paramStore)

  const columns = [
    {
      field: 'year',
      name: '',
    },
    {
      field: 'rentIncome',
      name: '',
      render: formatMoney,
    },
    // {
    //   field: 'emptyRoomLoss',
    //   name: '',
    //   render: formatMoney,
    // },
    {
      field: 'managementFee',
      name: '',
      render: formatMoney,
    },
    {
      field: 'maintainanceFee',
      name: '',
      render: formatMoney,
    },
    {
      field: 'yearlyCost',
      name: '',
      render: formatMoney,
    },
    {
      field: 'reformExpense',
      name: '',
      render: formatMoney,
    },
    {
      field: 'interest',
      name: '',
      render: formatMoney,
    },
    // {
    //   field: 'depreciationAssets',
    //   name: '',
    //   render: formatMoney,
    // },
    {
      field: 'depreciation',
      name: '',
      render: formatMoney,
    },
    {
      field: 'operatingIncome',
      name: '',
      render: formatMoney,
    },
    {
      field: 'carryOverLost',
      name: '',
      render: formatMoney,
    },
    {
      field: 'taxableIncome',
      name: '',
      render: formatMoney,
    },
    {
      field: 'tax',
      name: '',
      render: formatMoney,
    },
    {
      field: 'principalPayment',
      name: '',
      render: formatMoney,
    },
    {
      field: 'cashflow',
      name: 'CF',
      render: formatMoney,
    },
    {
      field: 'remainingAmount',
      name: '',
      render: formatMoney,
    },
    {
      field: 'accumulatedCashflow',
      name: 'CF',
      render: formatMoney,
    },
    {
      field: 'actualProfitRate',
      name: '',
      render: formatPercentage,
    },
  ]

  return (
    <EuiBasicTable
      items={items}
      columns={columns}
      compressed
      tableLayout="auto"
    />
  )
}

SimulatedTable.propTypes = {
  paramStore: PropTypes.objectOrObservableObject.isRequired,
}

export default inject('paramStore')(observer(SimulatedTable))
